import React, {useEffect, useState} from "react"
import {useApolloClient} from "@apollo/client";
import SignedIn from "./components/SignedIn";
import LoginForm from "./components/LoginForm"

const App = () => {
  const [token, setToken] = useState(null)
  const [manager, setOwner] = useState("")
  const [mobileView, setMobileView] = useState(false)
  const client = useApolloClient()
  console.log("Aloitetaan") 

  useEffect( () => {
    console.log("eseE App tässä")

    const token = localStorage.getItem("manager-token")
    const ownerH = localStorage.getItem("owner")
    const mobileOn = localStorage.getItem("mobile")

    if(token) {
      setToken(token)
    }

    if(ownerH) {
      setOwner(ownerH)
    }

    if(mobileOn===true) {
      setMobileView(mobileOn)
    }
  }, [])
  
  const logOut = () => {
    setToken(null)
    setOwner(null)
    localStorage.clear()
    client.resetStore()
  }
  const backLogOut = () => {
    logOut()
  }
  if (!token | !manager) {
    return (<LoginForm setToken={setToken} setOwner={setOwner} setMobileView={setMobileView} logOut={backLogOut} />)
  }
  //console.log(data)
  return(<SignedIn manager={manager} logOut={backLogOut} mobileView={mobileView} />)
}

export default App;
